
.playlist[data-v-5e89ceec]{
  width: inherit;
  height: inherit;
  box-shadow: -2px -2px 5px 2px rgb(194,194,195);
  border: 1px solid rgb(225,225,226);
  background-color: white;
}
.head[data-v-5e89ceec]{
  height: 40px;
  background-color: rgb(244,244,246);
  border-bottom: 1px solid rgb(225,225,226);
  text-align: center;
  padding: 5px 10px;
}
.head div[data-v-5e89ceec]{
  display: inline-block;
}
.choose[data-v-5e89ceec]{
  width: 220px;
  height: 25px;
  border: 1px solid rgb(225,225,226);
  text-align: center;
  line-height: 25px;
  border-radius: 20px;
  overflow: hidden;
}
.choose  div[data-v-5e89ceec]{
  width: 50%;
  float: left;
  background-color: white;
}
.close[data-v-5e89ceec]{
  float: right;
  cursor: pointer;
  font-size: 20px;
}
div.choosed[data-v-5e89ceec]{
  background-color: rgb(124,125,133);
  color: white;
}
.title[data-v-5e89ceec]{
  height: 30px;
  width: 100%;
  line-height: 30px;
  text-align: left;
  padding: 0 30px;
  border-bottom: 1px solid rgb(225,225,226);
}
.song-list[data-v-5e89ceec]{
  height: calc(100% - 70px);
  overflow-y: scroll;
  text-align: left;
}
li[data-v-5e89ceec]{
  height: 25px;
  line-height: 25px;
  padding: 0px 30px;
  position: relative;
}
li[data-v-5e89ceec]:nth-child(even){
  background-color: rgb(245,245,247);
}
li[data-v-5e89ceec]:hover{
  background-color: rgb(236,237,238);
}
li:hover span[data-v-5e89ceec]{
  color: black;
}
li:hover .delete[data-v-5e89ceec]{
  display: inline-block;
}
li span[data-v-5e89ceec]{
  display: inline-block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.singer[data-v-5e89ceec]{
  color: rgb(136,136,136);
}
.clear[data-v-5e89ceec],
.collect[data-v-5e89ceec]{
  float: right;
  display: inline-block;
  box-sizing: content-box;
  padding: 0px 10px;
  cursor: pointer;
}
.playing[data-v-5e89ceec]::before{
  content: " ";
  position: absolute;
  width: 0px;
  height: 0px;
  border: 5px solid transparent;
  border-left:7px solid rgb(198,47,47);
  top: 7px;
  left: 10px;
}
.delete[data-v-5e89ceec]{
  float: right;
  padding: 0px 5px;
  cursor: pointer;
  display: none;
}

/*定义滚动条高宽及背景 高宽分别对应横竖滚动条的尺寸*/
[data-v-5e89ceec]::-webkit-scrollbar {
  width: 10px;
  background-color: #F5F5F5;
}
/*定义滚动条轨道 内阴影+圆角*/
[data-v-5e89ceec]::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0);
  border-radius: 10px;
  background-color: rgb(245,245,247);
}
/*定义滑块 内阴影+圆角*/
[data-v-5e89ceec]::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgb(225,225,226);
  background-color: rgb(225,225,226);
}



