
.vue-slider-dot{
  background: url('../assets/point.png') no-repeat;
  box-shadow: none;
  border-radius: 0;
  position: relative;
}
.playprogress .vue-slider-dot{
  background-size: 5px 5px;
  background-position: 4px;
}
.volumeprogress .vue-slider-dot{
  background-size: 4px 4px;
  background-position: 3px;
}
.songsdetail{
  position: absolute;
  width: 204px;
  height: 63px;
  border: 1px solid #e1e1e2;
  top: -64px;
  left: -1px;
  background-color: #f6f6f8;
  border-bottom: none;
}
.songsdetail div{
  float: left;
  height: 63px;
  font-size: 12px;
  color: #333333;
  margin:3px;
  overflow:hidden;
}
.music-img{
  width: 50px;
  height: 63px;
}
.music-img img{
  width: 50px;
  height: 50px;
  margin:3px; 
  cursor: pointer;
}
.musicname{
  margin-top: 10px !important;
}
.musicname p{
  height: 22px;
  line-height: 28px;
  width: 140px;
  overflow:hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.musicname span{
  display: inline-block;
  width: 75px;
  height: 20px;
 
  overflow:hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.singername{
  padding: 3px;
}
.grey{
  color: #666;
}
.play-way{
  position: absolute;
  right: 65px;
  width: 45px;
  height: 20px;
  padding-right: 4px;
  text-align: right;
  background-image: url('../assets/playlist.jpg');
  background-position: 0px 0px;
  background-size: 20px 25px;
  background-repeat: no-repeat;
  margin-top: 10px;
  font-size: 12px;
  line-height: 20px;
  background-color: rgb(225,225,226);
  border-bottom-right-radius: 10px;
  border-top-right-radius: 10px;
  cursor: pointer;
  outline: none;
}



