
#list[data-v-44bbf50a]{
	height: 100%;
  background-color: rgb(245,245,247);
  overflow-y: scroll;
  border-right: 1px solid rgb(225,225,226)
}
dt[data-v-44bbf50a],dd[data-v-44bbf50a]{
  height: 30px;
  line-height: 30px;
  font-family: microsoft yahei;
  position: relative;
  font-size: 14px;
}
dd[data-v-44bbf50a]{
  text-indent: 20px;
  font-size: 13px;
  opacity: 0.8;
  color: black;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
dd[data-v-44bbf50a]:hover{
  opacity: 1.0;
  cursor: pointer;
}
dt[data-v-44bbf50a]{
  text-indent: 10px;
  color: rgb(125,125,125);
}
.icon[data-v-44bbf50a]{
  width: 20px;
  height: 20px;
  vertical-align: top;
  margin-right: 10px;
  margin-top: 5px;
}
.choosed[data-v-44bbf50a]{
  background-color: rgb(230,231,234);
  opacity: 1.0;
  color: black;
}
.choosed[data-v-44bbf50a]:before{
  position: absolute;
  left: 0px;
  top: 0px;
  display: inline-block;
  content: ' ';
  width:5px;
  height: 30px;
  background-color: rgb(198,47,47);
}


/*定义滚动条高宽及背景 高宽分别对应横竖滚动条的尺寸*/
[data-v-44bbf50a]::-webkit-scrollbar {
  width: 7px;
  background-color: #F5F5F5;
}
/*定义滚动条轨道 内阴影+圆角*/
[data-v-44bbf50a]::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0);
  border-radius: 10px;
  background-color: rgb(245,245,247);
}
/*定义滑块 内阴影+圆角*/
[data-v-44bbf50a]::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgb(225,225,226);
  background-color: rgb(225,225,226);
}



