
#contents[data-v-b43e3112]{
	height: 570px;
	width: 100%;
}
.left[data-v-b43e3112]{
  width: 20%;
  height: 100%;
  float: left;
}
.right[data-v-b43e3112]{
  width: 80%;
  height: 100%;
  float: right;
  overflow-x: hidden;
}

/*定义滚动条高宽及背景 高宽分别对应横竖滚动条的尺寸*/
[data-v-b43e3112]::-webkit-scrollbar {
  width: 10px;
  background-color: #F5F5F5;
}
/*定义滚动条轨道 内阴影+圆角*/
[data-v-b43e3112]::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0);
  border-radius: 10px;
  background-color: rgb(245,245,247);
}
/*定义滑块 内阴影+圆角*/
[data-v-b43e3112]::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgb(225,225,226);
  background-color: rgb(225,225,226);
}



