
*[data-v-1cdd1aac]{
    box-sizing: border-box;
}
#mains[data-v-1cdd1aac]{
  height: 100%;
  padding: 0 20px;
  overflow-y: scroll;
}
.navigation[data-v-1cdd1aac]{
  height: 40px;
  border-bottom: 1px solid rgb(225,225,226);
}
.navigation dl[data-v-1cdd1aac]{
  width: 60%;
  margin: 0 auto;
  overflow: hidden;
}
.navigation dd[data-v-1cdd1aac]{
  height: 40px;
  display: inline-block;
  float: left;
  width: 16.6%;
  text-align: center;
  line-height: 40px;
  font-size: 14px;
  color: black;
}
.navigation dd[data-v-1cdd1aac]:hover{
  color: rgb(198,47,47);
  cursor: pointer;
}
dd.choosed[data-v-1cdd1aac]{
  border-bottom: 2px solid rgb(198,47,47);
  color: rgb(198,47,47);
}
a[data-v-1cdd1aac]{
  text-decoration: none;
  outline: none;
}

/*定义滚动条高宽及背景 高宽分别对应横竖滚动条的尺寸*/
[data-v-1cdd1aac]::-webkit-scrollbar {
  width: 10px;
  background-color: #F5F5F5;
}
/*定义滚动条轨道 内阴影+圆角*/
[data-v-1cdd1aac]::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0);
  border-radius: 10px;
  background-color: rgb(245,245,247);
}
/*定义滑块 内阴影+圆角*/
[data-v-1cdd1aac]::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgb(225,225,226);
  background-color: rgb(225,225,226);
}



